<template>
  <div v-if="isDrizzleInitialized" >

    <div style="width:100%;overflow: hidden;position:absolute;top:0px;">
      <transition  name="fade" >
        <img v-show="hello" style="margin-left: 0;margin-top:100px;" src="../img/shou13.png">
      </transition>
      <transition  name="fade1" >
        <img v-show="hello1" style="margin-left: -100px;margin-top:0;" src="../img/shou12.png">
      </transition>
      <transition  name="fade2" >
        <img v-show="hello2" style="margin-left: 0;margin-top:-100px;" src="../img/shou10.png">
      </transition>
      <transition  name="fade3" >
        <img v-show="hello3" style="margin-right:0;float:right;margin-top:100px;" src="../img/shou16.png">
      </transition>

    </div>
    <div class="topbar">
       <router-link to="/searchrst">
        <button class="glyphicon glyphicon-search" style="opacity:0.7;float: right;margin-right: 10px;margin-top:170px; ">
        </button>
       </router-link>
      <input type="text" style="border-color:darkgray;margin-right: 10px;float:right;border-radius: 5px;margin-top: 170px;border-style:solid;border-width:1px;" placeholder="Search"/>
      <img src="../img/title.png" width="400" height="200" style="margin-right:300px;float:right;margin-top: 0;"/>
    </div>
    <div class="top2" style="color:darkgray;">
      <a style="margin-left: 450px; line-height:50px;float: left;color:darkgray;font-size: 17px;">Entertainment </a>
      <a style="margin-left: 50px; line-height:50px;float: left;color:darkgray;font-size: 17px;">Art </a>
      <a style="margin-left: 50px; line-height:50px;float: left;color:darkgray;font-size: 17px;">Design </a>
      <a style="margin-left: 50px; line-height:50px;float: left;color:darkgray;font-size: 17px;">Music</a>
      <a style="margin-left: 50px; line-height:50px;float: left;color:darkgray;font-size: 17px;">Education </a>
      <button class="btn btn-default" style=" margin-left: 255px; margin-top: 8px;float: left;">
        <router-link to="/login">Login</router-link>
      </button>
      <button class="btn btn-default" style="margin-left: 10px;margin-top: 8px;float: left;">
        <router-link to="/register">Register</router-link>
      </button>
    </div>
    <el-carousel :interval="4000" type="card" height="200px" style="position: absolute;overflow:hidden;top:650px;width: 100%;">
      <el-carousel-item v-for="(list,index) in Mlist" :key="index" >
        <img style="float: left;margin-left: 100px;" alt="" :src="list.images" @click="click_item(index)"/>
      </el-carousel-item>
    </el-carousel>
    <div class="l1" style="position: absolute;top:1000px;width: 60%;left: 47%;" v-show="list_word1">this is l1</div>
    <div class="l2" style="position: absolute;top:1000px;width: 60%;left: 47%;" v-show="list_word2">this is l2</div>
    <div class="l3" style="position: absolute;top:1000px;width: 60%;left: 47%;" v-show="list_word3">this is l3</div>
    <div class="l4" style="position: absolute;top:1000px;width: 60%;left: 47%;" v-show="list_word4">this is l4</div>
    <button class="btn btn-default" style="position:absolute;top:1100px;left:40%;width:20%;height:10%;">
      <router-link to="/createProject">Create my project</router-link>
    </button>

  </div>
  <div v-else>Loading...</div>
</template>

<script>
  import { mapGetters } from 'vuex'
export default {
name: "home",
  computed: mapGetters('drizzle', ['isDrizzleInitialized']),
  data(){
    return{
      h1s:false,
      Mlist:[
        {
          id:1,
          images:require("../img/lun1.jpg")
        },
        {
          id:2,
          images:require("../img/lun2.jpg")
        },
        {
          id:3,
          images:require("../img/lun3.jpg")
        },
        {
          id:4,
          images:require("../img/lun4.jpg")
        }
      ],
      list_word1:true,
      list_word2:false,
      list_word3:false,
      list_word4:false,
      hello:false,
      hello1:false,
      hello2:false,
      hello3:false

    }
  },
  methods:{
    handleScroll(e){
      this.hello=true;
      this.hello1=true;
      this.hello2=true;
      this.hello3=true;
    },
    click_item(index){
      if(index==0){
        this.list_word1=true;
        this.list_word2=false;
        this.list_word3=false;
        this.list_word4=false;
      }
      else if(index==1){
        this.list_word2=true;
        this.list_word1=false;
        this.list_word3=false;
        this.list_word4=false;
      }
      else if(index==2){
        this.list_word3=true;
        this.list_word2=false;
        this.list_word1=false;
        this.list_word4=false;
      }
      else if(index==3){
        this.list_word4=true;
        this.list_word2=false;
        this.list_word1=false;
        this.list_word3=false;
      }
    }
  },
  mounted() {
    window.addEventListener('mousewheel',this.handleScroll);

  }
}
</script>

<style scoped>
body{
  margin:0;
  overflow: hidden;
}
.topbar{
  position: absolute;
  top:300px;
  border-bottom-style: solid;
  border-color: lightgray;
  border-width:1px;
  width:100%;
  overflow: hidden;
}
.top2{
  overflow: hidden;
  height:50px;
  border-bottom-style: solid;
  border-color: lightgray;
  border-width:1px;
  position: absolute;
  top:500px;
}
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.fade1-enter-active, .fade1-leave-active {
  transition: opacity .5s;
}
.fade1-enter, .fade1-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.fade2-enter-active, .fade2-leave-active {
  transition: opacity .5s;
}
.fade2-enter, .fade2-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.fade3-enter-active, .fade3-leave-active {
  transition: opacity .5s;
}
.fade3-enter, .fade3-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
