<template>
  <div v-if="isDrizzleInitialized" class="body">
    <el-menu style="position: fixed;top:0px;right:0;background-color:rgba(255,255,255,0)" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <el-menu-item index="1"><router-link to="/home">首页</router-link></el-menu-item>
    </el-menu>
    <img style="margin-top: -50px; margin-left: 0px;" src="../img/hand3.png"/>
    <img style="margin-top: 0px;margin-right: 0px;float: right;" src="../img/twohands.png"/>
    <img style="margin-top: 300px;margin-left: -800px;" src="../img/hand6.png"/>
    <div style="position:absolute;top:200px;left:550px;">

    </div>

    <div class="ins">
      <div>
        <router-link to="/center" style="float:left;">
          <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
        </router-link>
        <foreign-area class="choose" popularCity="受欢迎的国家" selectBg="选择器的背景颜色" groupBg="选项的背景颜色" @selectCountry="监听选中的国家"></foreign-area>
        <el-input v-model="input" placeholder="project name" style="float: left;width:178px;"></el-input>
        <el-date-picker style="float: left;"
          v-model="value1"
          type="datetime"
          placeholder="选择日期时间">
        </el-date-picker>
        <el-button plain style="float: left;width:100px;" >submit</el-button>
      </div>

      <div>
        <textarea style="width:100%;height:200px;border-color: white;border-radius: 5px;margin-top:10px;" placeholder="enter your detail"></textarea>
      </div>

    </div>



  </div>
  <div v-else>Loading...</div>
</template>

<script>
import foreignArea from "./foreignArea.vue" ;
import { mapGetters } from 'vuex'
export default {

  name: "createProject",
  computed: mapGetters('drizzle', ['isDrizzleInitialized']),
  data(){
    return{
      options: [{
        value: '选项1',
        label: 'art'
      }, {
        value: '选项2',
        label: 'education'
      }, {
        value: '选项3',
        label: 'environment'
      }
      ],
      value: '',
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '昨天',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '一周前',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      },
      value1: '',
      value2: '',
    }
  },
  components:{'foreign-area':foreignArea}

}

</script>

<style scoped>
.body{
  background: url("../img/stone.png") no-repeat;
  background-size: 100% 1000px;
  overflow: hidden;
}
.ins{
  border-radius: 3px;
  position: absolute;
  top:250px;
  right:50px;
  overflow: hidden;
  width:800px;
  height:400px;
}
.choose{
  margin-left: 10px;
  float: left;
  width:auto;
}
textarea::-webkit-input-placeholder{
  color:#C4C4CC;
}

</style>
