
<template>

<div v-if="isDrizzleInitialized" class="mybody">
 <div class="Avatar">
 <el-row class="user-avatar">
 <el-col :span="12">
      <div class="avatar--square">
        <div class="block"><el-avatar shape="square" :size="120" :src="squareUrl" ></el-avatar></div>
      </div>
 </el-col>
  </el-row>
 </div>

  <el-menu style="position: fixed;top:0px;right:0px;background-color:rgba(255,255,255,0.6)" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
    <el-menu-item index="1"><router-link to="/home">首页</router-link></el-menu-item>
    <el-submenu index="2">
      <template slot="title">未定</template>
      <el-menu-item index="2-1"><router-link to="/center">个人中心</router-link></el-menu-item>

      <el-menu-item index="2-2">选项2</el-menu-item>
      <el-menu-item index="2-3">选项3</el-menu-item>
    </el-submenu>
  </el-menu>
<div class='info'>
  <el-tabs type="border-card" style="position:absolute;height:100%;width:100%;overflow:auto">
  <el-tab-pane>
    <span slot="label">个人信息</span>
        <div>
        <el-card>
          <p>用户昵称：</p>
        </el-card>
        <el-card>
            <p>头像修改</p>
                <el-upload
                   class="avatar-uploader"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload">
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
        </el-card>
    </div>
  </el-tab-pane>
  <el-tab-pane label="消息中心">消息中心</el-tab-pane>
  <el-tab-pane label="项目追踪">项目追踪
     <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
  </el-tab-pane>
  <el-tab-pane label="余额管理"  >
    余额管理
    </el-tab-pane>
</el-tabs>
</div>

</div>
    <div v-else>Loading...</div>

</template>

<script>
    import { mapGetters } from 'vuex'

  export default {
      computed: mapGetters('drizzle', ['isDrizzleInitialized']),
    data () {
      return {
        squareUrl: ("../img/center/center_toux.png"),
        sizeList: "large",
        imageurl:''

      }
    },
      methods: {
    // 移除图片
    handleRemove() {
      this.imageUrl = ''
    },
    // 上传成功回调
    handleAvatarSuccess(res, file) {
      this.imageUrl = res.data.url
    },
    // 上传前格式和图片大小限制
    beforeAvatarUpload(file) {
      const type = file.type === 'image/jpeg' || 'image/jpg' || 'image/webp' || 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!type) {
        this.$message.error('图片格式不正确!(只能包含jpg，png，webp，JPEG)')
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!')
      }
      return type && isLt2M
    }

  }
  }
</script>

<style scoped>

.mybody{
    background: url("../img/center/center_bg.jpg") no-repeat;
    background-size:cover;
    width:100%;
    height:100%;/*宽高100%是为了图片铺满屏幕 */
    z-index:-1;
    position: absolute;
    overflow: scroll;
}

.block{
  position: absolute;

  top:30px;
  left:50px;
  width:400px;
  height:500px;
}
.avatar--square{
  position:absolute;
  top:15px;
  left:130px;
}
.info{
  position: absolute;
  top: 250px;
  left:120px;
  width: 1000px;
  height:100%;
  background-color: rgba(255,255,255,0.6);
}
.avatar-uploader{
  width: 120PX;
  height: 120px;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  background: url('../img/center/center_toux.png') no-repeat;
  background-size: 100% 100%;
}
.avatar-uploader-icon{
  font-size: 0;
  color: #fff;
  width: 120px;
  height: 120px;
  line-height: 120px;
  text-align: center;
}
.avatar-uploader-icon:hover{
  font-size: 28px;
  background-color: rgba(0, 0, 0, .3);
}
.avatar {
  position: relative;
  width: 120px;
  height: 120px;
  display: block;
}
.el-upload-action {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  font-size: 0;
  color: #fff;
  text-align: center;
  line-height: 120px;

}
.el-upload-action:hover {
  font-size: 20px;
  background-color: #000;
  background-color: rgba(0, 0, 0, .3)
}
</style>

